html {
    min-height: 100%;
    background: linear-gradient(#99CCCC, #9966ff);
}

body {
    color: #fff;
    font-weight: bold;
    text-align: center;
}
header {
  color: #9966CC;
  width: 100%;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.left {
  -webkit-order: 1;
          order: 1;
  width: 150px;
}

article {
  -webkit-order: 2;
          order: 2;
  max-width: 600px;
  margin: 5px;
}

.right {
  -webkit-order: 3;
          order: 3;
  width: 150px;
}
.game-board {
  clear: both;
  display: table;
  border: 1px solid #dadada;
  margin: 0 auto;
  padding: 0;
}

.game-board:focus {
  outline: none;
}

@media all and (min-width: 794px){
  .game-board  {
      -webkit-flex: 2 1;
              flex: 2 1;
  }
}
.box {
  list-style: none;
  background: #f1f1f1;
  border: 1px solid #ccc;
  float: left;
  box-sizing: border-box;
  width: 185px;
  height: 185px;
  padding: 0;
  text-align: center;
}

.box>span {
  font-size: 150px;
}

.player-x {
  color: #388e3c;
}

.player-o {
  color: #ff3399;
}

.box.cursor-player-x:hover {
  cursor: url(data:image/png;base64,), pointer;
}

.box.cursor-player-o:hover {
  cursor: url(data:image/png;base64,), pointer;
}

.box.cursor-moving-disabled:hover {
  cursor: not-allowed;
}

.box.moving-enabled:hover {
  background: #fff;
}

.box.moving-disabled:hover {
  background: #f1f1f1;
}

.box.winning-box,
.box.winning-box:hover{
    background: #ffff99;
    cursor: not-allowed;
}
.reset-game {
    height: 30px;
    width: 150px;
    border-radius: 4px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    border: none;
    background: #ff99cc;
    color: #fff;
    letter-spacing: 1px;
    cursor: pointer;
    margin-top: 5px;
}

.reset-game:hover {
    background: #2c3e52;
}
h2 {
    color: #9977ff;
}
.space-between-elem {
    margin-left: 10px;
}
.score-highlight{
    color: #ff6600;
}
.status-component>span {
    color: #ed4e6e;
    font-size: 3em;
}
